<template>
    <section>
        <loader :is-visible="isLoading"></loader>
            <tg-list ref="tgList" v-show="IsJobPostView" :IsShowAction="true" ModuleName="HR" SubModuleCode="HR_JOB" :RenderRowActionMethod="renderActions" 
            v-on:ActionButtonClick="actionButtonClick" IdentityColumn="post_id" :HeaderText="$t('JobListing')" :listType="['List', 'Card']"
            :ListData="JobData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" v-on:ApplyView="OnApplyView"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :ViewList="ViewList"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
    
            <template v-slot:slotdata="{ data }">   
                <template v-if="data.column.COLUMN_NAME == 'post_title'"> 
                    <a href="javascript:;" @click="ViewJobPostDetail(data.row.post_id)">{{data.row.post_title }}</a>
                 </template>
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id == 'Draft'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id == 'Internal'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id == 'Private'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')" v-if="data.row.status_id == 'Publish'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </template>
            </template>
    
            <template v-slot:customcard="{ data }">
                <div v-bind:class="{ 'card-box green-bg-light': true,'samplebar_card': data.row.is_sample_data ==true}">
                    <div class="card-number">
                        <div class="custom-control custom-checkbox custom-control-inline" style="">
                                <input :id="data.row.post_id" class="chkItems custom-control-input"
                                    @change="ForCheckBoxClear($event)" v-bind:value="data.row.post_id" type="checkbox"/>
                                <label :for="data.row.post_id" type="checkbox" class="custom-control-label"></label>
                            </div>  {{ truncateText(data.row.post_title,15) }}
                        <span class="float-right">
                            <a v-if="data.row.status_name=='Publish'" href="javascript:;" class="round-icon-small bg-primary d-flex align-items-center justify-content-center text-white"
                            @click="HideCardAction(this)" title="Share"><i class="fa  fa-share-alt float-right"></i></a>
                            <a v-if="data.row.status_name!='Publish'" class="round-icon-small bg-primary d-flex align-items-center justify-content-center text-white disabled" 
                            title="Share"><i class="fa  fa-share-alt float-right"></i></a>
                        </span>
                    </div>
                    <div class="card-detail-main filterscrollForCardView" v-if="summary">
                        <p>
                            <span class="w-100 float-left" id="multiple-events">
                                <span class="w-100 float-left mb-1">
                                    <span class="float-left">{{truncateText(data.row.short_desc,15)}}</span>
                                    <a href="javascript:;" class="text-primary" v-if="data.row.short_desc !== null && data.row.short_desc.length > 200" @click="summary = false">Read more....</a>
                                </span>
                            </span>
                        </p>
                    </div>
                    <div class="card-detail-main cb-event-list" style="display:none;" v-else>
                        <p>
                            <span class="w-100 float-left" id="multiple-events">
                                <span class="w-100 float-left mb-1">
                                    <span class="float-left">{{data.row.short_desc}}</span>
                                </span>
                            </span>
                        </p>
                    </div>
                    <div class="card-detail-main">
                        <div class="card-heading">
                            <a @click="ViewJobPostDetail(data.row.post_id)"  
                                class="text-primary link">{{data.row.job_title}}</a>
                        </div>
                        <div class="card-detail"><i class="fa fa-file" aria-hidden="true"></i> <span>{{data.row.job_type_id}}</span></div>
                        <div class="card-detail"><i class="fa fa-calendar-o" aria-hidden="true"></i> <span>{{formatDate(data.row.start_date)}} - {{formatDate(data.row.close_date)}}</span></div>
                        <div class="card-date">
                            <i class="fa fa-user" aria-hidden="true" style="font-size:17px;"></i>&nbsp;Applicant <span>
                                <span v-if="data.row.applicant_count==0" class="text-primary link px-1">{{data.row.applicant_count}}<span></span></span>
                                <a target="_self" v-if="data.row.applicant_count>0" v-bind:href="'/JobPost/JobDetail?id='+data.row.post_id+'&DisplayTab=CandidateDetails'" 
                                    class="text-primary link px-1">{{data.row.applicant_count}}<span></span></a>
                            </span>
                        </div>
                        <div class="card-detail"><i class="fa fa-user-circle-o" aria-hidden="true"></i> <span>{{data.row.department_id}} </span></div>
                    </div>
                    <div class="bottom-action py-0 h-10">
                        <div class="card-date mb-0"><i class="fa fa-globe text-success pr-1" aria-hidden="true"></i> <span>{{data.row.status_name}}</span></div>
                        <transition>
                            <span class="text-success z-indexfix linkcopied lcard" v-if="copiedSuccess">
                                {{ $t('CopiedSuccessfully') }}
                            </span>
                        </transition>
                        <div class="card-action mb-0 w-100  my-1">
                            <a href="javascript:;" class="cb-eventlist px-2 bg-white border float-right" onclick="ShowDescription(this)"><span>Description<i class="fa fa-caret-down pl-1 text-primary"></i></span></a>
                            <a class="round-icon-small btn-danger" v-bind:class="{'fa fa-trash text-white disabled':data.row.IsAssociated!='0','fa fa-trash text-white':data.row.IsAssociated=='0'}" 
                            v-on:click="commonDeleteData(data.row.post_id)" title="Delete"></a>
                            <a href="javascript:;" v-on:click="ExternalLink(data.row)" class="fa fa-link btn-info text-white" v-bind:class="{'round-icon-small btn-info text-white':data.row.status_name!='Draft' 
                                && data.row.status_name!='Internal','round-icon-small btn-info text-white ':data.row.status_name=='Draft' || data.row.status_name=='Internal'}" 
                                v-bind:title="'Click to copy the link'">
                                <i class="fa fa-copy text-white"></i>
                            </a>
                            <a @click="EditJobPost(data.row.post_id)"  
                            target="_self" class="round-icon-small btn-success text-white" title="Edit"><i class="fa fa-pencil text-white"></i></a>
                            <a @click="openAddNewCandidate(data.row.post_id)" v-bind:class="{'round-icon-small btn-primary text-white'
                            :data.row.status_name!='Draft' && data.row.status_name!='Private','round-icon-small btn-primary text-white disabled':data.row.status_name=='Draft' || data.row.status_name=='Private'}" 
                            title="Add Candidate"><i class="fa fa-user-plus text-white"></i></a>
                            <a @click="ViewJobPostDetail(data.row.post_id)" 
                             target="_self" style="background-color: #7898d9;" class="round-icon-small btn-secondary text-white" v-bind:value="data.row.deal_id" title="View">
                                <i class="fa fa-eye text-white"></i>
                            </a>
                        </div>
                    </div>
                </div>
            </template>
        </tg-list>
        <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="HR_JOB" modulename="Hiring" v-on:close="ShowManageView = false" 
            v-bind:callbackfunction="FetchData" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </viewlayout>
    </section>
    </template>
    
    <script>
    import Schema from '../JobPost/Schema';
    import DataService from '../../../services/DataService';
    import viewlayout from "../../../components/Common/Modal/view-layout.vue";
    import moment from 'moment'
    export default {
        components: {
            viewlayout
        },
        data() {
            return {
                isLoading: false,
                search: '',
                showgrid: true,
                JobData: [],
                ViewList: [],
                listheaderbuttons: Schema.buttons,
                leftSearchFields: Schema.leftsearchSchema,
                listActions: Schema.listActions,
                TotalRecord: 0,
                SortExp: '',
                SortBy: '',
                PageNumber: 1,
                PageSize: 10,
                TotalPages: 0,
                CurrentPage: 1,
                posttitle: '',
                deptname: '',
                jobtype: '',
                searchCondition: '',
                Candidate_IDS: '',
                Headers: [],
                user_id: null,
                companyId: null,
                ShowAddJob: false,
                ShowManageView: false,
                user_type: null,
                IsJobPostView: true,
                externalUrl: '',
                internalUrl: '',
                storeFrontUrl:'http://hr.{subdomain}.com/',
                copiedSuccess: false
            }
        },
        async created() {
            var vm = this;
            vm.user_id = this.GetUserInfo.ID
            vm.user_type = this.GetUserInfo.UserType
            vm.companyId = this.GetUserInfo.CompanyId;
            vm.CompanyName = vm.GetUserInfo.CompanyName;
            await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "DEPARTMENT");
            await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "HR_JOB_TYP");
            await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "JOBPOST.start_date || JOBPOST.close_date");
            await vm.FetchData();
            await vm.GetDdlRecords_ViewList();
           
        },
        methods: {
            formatDate: function (value) {
                if (value) {
                    return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
                }
            },
            ForCheckBoxClear: function (ths) {            
                var vm = this;
                var val = "";
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
                if (val.length > 0) {               
                    vm.listheaderbuttons[1].isdisabled = false;
                }
                else {                
                    vm.listheaderbuttons[1].isdisabled = true;
                }
            },
            clickEventOfColumn: function (value, column) {
                var vm = this;
                if (column.COLUMN_NAME == 'job_title') {
                    vm.$router.push({ name: 'JobPost', params: { id: value.post_id, type: 'JobPostDetail' } });
                }
            },
            ViewJobPostDetail: function (value) {
                var vm = this;
                vm.$router.push({ name: 'JobPost', params: { id: value, type: 'JobPostDetail' } });
            },
            leftCommonSearch: function (value) {
                this.PageNumber = value.PageNumber;
                this.isPaged = 1;
                if (typeof value.searchCondition != 'undefined') {
                    this.searchCondition = value.searchCondition;
                } else {
                    this.searchCondition = "";
                }
                this.FetchData();
            },
            pagerMethod: function (value) {
                this.PageSize = value.PageSize;
                this.PageNumber = value.PageNumber;
                this.FetchData();
            },
            sortdata: function (s) {
                var SortDir = "sort tb_headerSortDown";
                this.SortBy = s;
                if (s === this.SortBy) {
                    if (this.SortExp == "") {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    } else if (this.SortExp == "ASC") {
                        this.SortExp = "DESC";
                        SortDir = "sort tb_headerSortUp";
                    } else {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                }
                this.FetchData();
                $("#" + s + '  span:first-child').attr("class", SortDir);
            },
            commonHeaderButtonClick: function (event) {
                switch (event.callbackfunction) {
                    case "commonDeleteData":
                        this.commonDeleteData();
                        break;
                    case "ManageView":
                        this.ManageView();
                        break;
                }
            },
            actionButtonClick: function (callbackfunction, item) {
                switch (callbackfunction) {
                    case 'Edit':
                        this.EditJobPost(item.post_id)
                        break;
                    case 'openAddNewCandidate':
                        this.openAddNewCandidate(item.post_id)
                        break;
                    case 'ClickOnView':
                        this.ViewJobPostDetail(item.post_id);
                        break;
                    case 'Clone':
                        this.EditJobPost(item.post_id)
                        break;
                    case 'ExternalLink':
                        this.ExternalLink(item)
                        break;
                    case 'Share':
                        this.Share(item)
                        break;
                }
            },
            EditJobPost: function (post_id) {
                var vm = this;
                if (post_id > 0) {
                    var id = post_id;
                    var moduleName = 'Hiring';
                    var subModuleCode = 'HR_JOB';
    
                    vm.$router.push({
                        name: 'HRManageJobPost',
                        params: {
                            id: id,
                            moduleName: moduleName,
                            subModuleCode: subModuleCode,
                            reqfrom: '/JobPost/JobListing'
                        }
                    });
                }
            },
            openAddNewCandidate: function (ths) {            
                this.$router.push({ name: 'ManageCandidate', params: { reqfrom: 'JobPost/JobListing', id: 0, pid: ths, aid: null, uid: null } });
            },
            renderActions: function (row) {
                var self = this;
                let rtnActionArray = [];
                self.listActions.forEach(actionItem => {
                    let action = {
                        ...actionItem
                    };
                    switch (action.title) {
                        case 'Share':                        
                            action.isVisible = row.status_name == 'Publish' ? true : false;
                            break;
                        case 'Clone':
                            action.isVisible = true;
                            break;
                        case 'View':
                            action.isVisible = true;
                            break;
                        case 'External Link':
                            action.isVisible = row.status_name != 'Draft', 'Private' ? true : false;
                            break;
                        case 'Add New Candidate':
                            action.isVisible = row.status_name != 'Draft', 'Private' ? true : false;
                            break;
                        case 'Edit':
                            action.isVisible = true;
                            break;
                        default: {
                            action.isVisible = true;
                        }
                    }
                    rtnActionArray.push(action);
                });
                return rtnActionArray;
            },
            FetchData: async function () {
                var vm = this;
                var params = `posttitle=${vm.posttitle}&deptname=${vm.deptname}&jobtype=${vm.jobtype}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&totalRecords=${vm.TotalRecord}&searchCondition=${vm.searchCondition}&Candidate_IDS=${vm.Candidate_IDS}`
                await DataService.JobListing(params).then((response) => {              
                    if (response.data != '') {
                        vm.Headers = response.data.schema;                    
                        vm.Headers.forEach((column) => {                        
                            if (column.DATA_TYPE == 'date') {
                                column.DATA_TYPE = 'DATE';
                            }
                            if (column.DATA_TYPE == 'select' || column.DATA_TYPE == 'string'||column.DATA_TYPE == 'decimal') {
                                column.DATA_TYPE = 'text';
                            }  
                        });
                        vm.JobData = response.data.data;
                        for (var row in vm.JobData) {
                            vm.JobData[row].isCheckBoxDisabled = false;
                            if (vm.JobData[row].IsAssociated == 1) {
                                vm.JobData[row].isCheckBoxDisabled = true;
                            }
                        }
                        vm.Headers.forEach((column) => {
                            column.DISPLAY_NAME = vm.$t(column.DISPLAY_NAME ? column.DISPLAY_NAME : column.COLUMN_NAME);
                            column.settings = null;
                            if (column.COLUMN_NAME == 'job_title') {
                                column.settings = {
                                    clickEvent: vm.clickEventOfColumn,
                                    formatter: vm.columnDataFormatter
                                }
                            }
                            if (column.COLUMN_NAME == "status_id") {
                                column.settings = {
                                    isInSlot: true,
                                }
                            }
                            if (column.COLUMN_NAME == "post_title") {
                                column.settings = {
                                    isInSlot: true,
                                }
                            }
                        })
    
                        vm.JobData.forEach(function (row) {
                                if(row.is_sample_data === true){
                                    row.additionalClass = "sample-bar";
                                }
                            });
    
                        vm.TotalRecord = response.data.data[0].total_records;
                        vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                    }
                    document.querySelector('input[placeholder="DateFrom"]').setAttribute('placeholder', 'Date From');
                    document.querySelector('input[placeholder="DateTo"]').setAttribute('placeholder', 'Date To');
                })
            },
            commonDeleteData: function (event) {
                var vm = this;
                var val = "";
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
                if (val.length > 0) {
                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Crew")), function () {
                        var postJSON = {
                            ids: val,
                            companyId: vm.companyId,
                            userId: vm.user_id,
                            moduleName: "Hiring",
                            subModuleCode: "HR_JOB"
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        DataService.CommonCommonDelete(objectPostString).then(response => {
                            if (response.data[0].Status == "Success") {
                                vm.FetchData();
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Job')]), "Success", true, vm.$t('Alert'));
                            } else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Job')]), "failure", true, vm.$t('Alert'));
                            }
                        });
                    });
                } else {
                    alert(vm.$t("Selectanyrecordtodelete"));
                }
            },
            ManageView: function () {
                var vm = this;
                vm.ShowManageView = true;
                setTimeout(function () {
                    $("#modalManageView").modal("show");
                    setTimeout(function () {
                        vm.SetBootstraptoolTip();
                    }, 200);
                }, 1000);
            },
            DefaultView: function () {
                var vm = this;
                var obj = {
                    ModuleName: 'Hiring',
                    SubmoduleCode: 'HR_JOB'
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ClearView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                        }
                    } else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                });
            },
            OnApplyView(viewId) {
                var vm = this;
                if (viewId == '0') {
                    vm.DefaultView()
                } else {
                    var obj = {
                        ViewId: viewId,
                        ModuleName: 'Hiring',
                        SubmoduleCode: 'HR_JOB'
                    };
                    var objectPostString = JSON.stringify(obj);
                    DataService.ApplyView(objectPostString).then(function (response) {
                        if (response.data != null) {
                            if (response.data.code == "1" || response.data.Code == "1") {
                                vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                                vm.$emit('RefreshManageView');
                                vm.FetchData();
                            } else {
                                vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                            }
                        } else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    });
                }
            },
            GetDdlRecords_ViewList: async function () {
                var vm = this;
                vm.ViewList = [];
                var url = `moduleName=Hiring&subModuleCode=HR_JOB&viewId=0&pageSize=100&pageNum=1&sortBy=&sortExp=`;
                await DataService.GetViewList(url).then(function (response) {
                    if (response.data && typeof (response.data.DATA) != 'undefined') {
                        vm.ViewList = response.data.DATA;
                    }
                    var chkVACTIVE = 0;
                    vm.ViewListId = "0";
                    if (vm.ViewList == null || vm.ViewList == "") {
    
                    } else {
                        vm.ViewList.forEach(function (item) {
                            if (item.IS_SLECTED_USER_ID > 0) {
                                chkVACTIVE = chkVACTIVE + 1;
                                vm.ViewListId = item.VIEW_ID;
                            }
                        });
                    }
                    var indx = 0;
                    if (chkVACTIVE > 0) {
                        vm.ViewList.splice(indx, 0, {
                            "SUB_MODULE_NAME": "",
                            "SUB_MODULE_CODE": "",
                            "VIEW_NAME": "Default View",
                            "VIEW_ID": 0,
                            "IS_APPLICABLE_TO_ALL": 1,
                            "MODULE_NAME": "",
                            "CREATED_AT": "",
                            "IS_SLECTED_USER_ID": 0,
                            "CAN_EDIT": 0,
                            "ROWNUMBER": 0,
                            "VIEW_TYPE": "",
                            "USER_LIST": ""
                        });
                    } else {
                        if (vm.ViewList == null || vm.ViewList == "") {
                            vm.ViewList.push({
                                "SUB_MODULE_NAME": "",
                                "SUB_MODULE_CODE": "",
                                "VIEW_NAME": "Default View",
                                "VIEW_ID": 0,
                                "IS_APPLICABLE_TO_ALL": 1,
                                "MODULE_NAME": "",
                                "CREATED_AT": "",
                                "IS_SLECTED_USER_ID": 1,
                                "CAN_EDIT": 0,
                                "ROWNUMBER": 0,
                                "VIEW_TYPE": "",
                                "USER_LIST": ""
                            });
                        } else {
                            vm.ViewList.splice(indx, 0, {
                                "SUB_MODULE_NAME": "",
                                "SUB_MODULE_CODE": "",
                                "VIEW_NAME": "Default View",
                                "VIEW_ID": 0,
                                "IS_APPLICABLE_TO_ALL": 1,
                                "MODULE_NAME": "",
                                "CREATED_AT": "",
                                "IS_SLECTED_USER_ID": 1,
                                "CAN_EDIT": 0,
                                "ROWNUMBER": 0,
                                "VIEW_TYPE": "",
                                "USER_LIST": ""
                            });
                        }
                    }
                    setTimeout(() => {
                        if (vm.ViewListId > 0) {
                            vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                        } else {
                            vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                        }
                    }, 1000);
    
                });
            },
            getStatusClassList: function (status) {
                var returnClass = '';
                switch (status) {
                    case 'Draft':
                        returnClass = 'px-2 text-center text-white d-block bg-success';
                        break;
                    case 'Internal':
                        returnClass = 'px-2 text-center text-white d-block bg-danger';
                        break;
                    case 'Private':
                        returnClass = 'px-2 text-center text-white d-block bg-danger';
                        break;
                    case 'Publish':
                        returnClass = 'px-2 text-center text-white d-block bg-primary';
                        break;
                    default:
                        returnClass = '';
                }
                return returnClass;
            },
            ExternalLink: async function (item) {
                var vm=this;
                vm.externalUrl = this.storeFrontUrl.replace("{subdomain}", this.GetUserInfo.CompanyName.replace(" ", "-").replace(".", "dot")) + "/Default/JobDetail?cid=" + this.GetUserInfo.CompanyId + "&pid=" + item.post_id;
                var text=vm.externalUrl;
                try { 
                        vm.copiedSuccess = true;
                     await navigator.clipboard.writeText(text);
                   
                    setTimeout(() => {
                     this.copiedSuccess = false;
                     }, 2000);
                } catch (err) {
                    console.error('Failed to copy: ', err);
                }
        },  
            Share(platform) {
                const baseUrl = 'YourBaseURL'; 
                const postId = platform.post_id;
                const newExtUrl = `${baseUrl}/${postId}`; 
                let shareUrl = '';
                switch (platform) {
                    case 'facebook':
                        shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(newExtUrl)}`;
                        break;
                    case 'linkedin':
                        shareUrl = `https://www.linkedin.com/shareArticle?mini=true&url=${encodeURIComponent(newExtUrl)}`;
                        break;
                    case 'twitter':
                        shareUrl = `https://twitter.com/intent/tweet?url=${encodeURIComponent(newExtUrl)}`;
                        break;
                    case 'whatsapp':
                        shareUrl = `https://api.whatsapp.com/send?text=${encodeURIComponent(newExtUrl)}`;
                        break;
                    default:
                        return '';
                }
                window.open(shareUrl, '_blank');
            }
        }
    }
    </script>
    